<template>
  <div class="vue-image-wraper">
    <img :src="items[currentItem].filters[0].items[selectedItems['']].src" 
      class="vue-image vue-image-walls" />
    <img 
      v-if="items[currentItem].filters[1] !== undefined"
      :src="items[currentItem].filters[1].items[selectedItems['']].src" 
      class="vue-image vue-image-flor" />
    <img 
      v-if="items[currentItem].filters[1] !== undefined"
      :src="items[currentItem].filters[2].items[selectedItems['']].src" 
      class="vue-image vue-image-doors" />
    <img 
      v-if="items[currentItem].filters[1] !== undefined && selectedItems[''][0]"
      :src="items[currentItem].filters[3].items[0].src" 
      class="vue-image vue-image-additionals" />
    <img 
      v-if="items[currentItem].filters[1] !== undefined && selectedItems[''][1]"
      :src="items[currentItem].filters[3].items[1].src" 
      class="vue-image vue-image-additionals" />
    <img 
      v-if="items[currentItem].filters[1] !== undefined && selectedItems[''][2]"
      :src="items[currentItem].filters[3].items[2].src" 
      class="vue-image vue-image-additionals" />
    <img 
      v-if="items[currentItem].filters[1] !== undefined && selectedItems[''][3]"
      :src="items[currentItem].filters[3].items[3].src" 
      class="vue-image vue-image-additionals" />
    <img 
      v-if="items[currentItem].filters[1] !== undefined && selectedItems[''][4]"
      :src="items[currentItem].filters[3].items[4].src" 
      class="vue-image vue-image-additionals" />
    <img 
      v-if="items[currentItem].filters[1] !== undefined && selectedItems[''][5]"
      :src="items[currentItem].filters[3].items[5].src" 
      class="vue-image vue-image-additionals" />
    <img 
      v-if="items[currentItem].filters[1] !== undefined && selectedItems[''][6]"
      :src="items[currentItem].filters[3].items[6].src" 
      class="vue-image vue-image-additionals" />
    <img 
      v-if="items[currentItem].filters[1] !== undefined && selectedItems[''][7]"
      :src="items[currentItem].filters[3].items[7].src" 
      class="vue-image vue-image-additionals" />
    <img 
      v-if="items[currentItem].filters[1] !== undefined && selectedItems[''][8]"
      :src="items[currentItem].filters[3].items[8].src" 
      class="vue-image vue-image-additionals" />
    <img 
      v-if="items[currentItem].filters[1] !== undefined && selectedItems[''][9]"
      :src="items[currentItem].filters[3].items[9].src" 
      class="vue-image vue-image-additionals" />
  </div>
</template>

<script>
export default {
  name: 'TheImageBuilder',
  computed: {
    currentItem() {
      return this.$store.state.current_item
    },
    selectedItems() {
      return this.$store.state.selected[this.currentItem];
    }
  },
  props: {
    items: {
      type: Array,
      required: true
    }
  }
}
</script>





