$(document).ready(function() {
  if (window.matchMedia('(min-width: 768px)').matches){
    $(".buffer").stick_in_parent();
  }


});

$('.tour__body-overlay').click(function (){
  var wrapp = $('.tour__body-overlay');
  console.log(wrapp);
  wrapp.fadeOut(500);
});



