export default function initCommercialProperty() {
  let slickObj = $('.commercial__nav');
  let slick = slickObj.slick('getSlick');
  let nextHolder = $('.commercial__gallery .navigation__build-btn_next .arrow__desk');
  let prevHolder = $('.commercial__gallery .navigation__build-btn_prev .arrow__desk');

  function setDesc(slideTo){

    let nextIndex = slideTo + 1 == slick.slideCount ? 0 : slideTo + 1;
    let prevIndex = slideTo - 1 == -1 ? slick.slideCount - 1 : slideTo - 1;

    let nextDesc = slick.$slides.eq(nextIndex).text();
    let prevDesc = slick.$slides.eq(prevIndex).text();

    nextHolder.text(nextDesc);
    prevHolder.text(prevDesc);
  }

  // initial set
  setDesc(0);

  // On before slide change
  slickObj.on('beforeChange', function(event, slick, slideCurrent, slideTo) {
    setDesc(slideTo);
  });

};



