;(function(){
window.forms = {
	init: function() {

		var th = forms;


		th.events();

	},
	events: function() {

		var th = forms;

		$(document).on('blur', 'input', function() {
			var $inp = $(this);
			var $field = $inp.closest(".form__field");
			if ($inp.val().length) {
				$field.addClass("form__field_has_content");
			} else {
				$field.removeClass("form__field_has_content");
			}
		});

		$(document).on('focusout', 'textarea', function() {
			var $inp = $(this);
			var $field = $inp.closest(".form__field");
			if ($inp.val().length) {
				$field.addClass("form__field_has_content");
			} else {
				$field.removeClass("form__field_has_content");
			}
		});
	}
};
})();



