$(document).ready(function() {

  $('.vac-faq__link').click(function(e) {
    e.preventDefault();
    e.stopPropagation();
    if ( e.target !== this ) return;
    $(this).toggleClass('active');
    $(this).parent().find('.vac-faq__textblock').toggle('fast');
  });

  $('.commercial__mask-link').click(function(e){
    e.stopPropagation();
  })

});



