<template>
  <div class="vue-card wrap wrap_mobile_full">
     <div class="card-items">
      <div class="card-items__col"
        :key="i"
        v-for="(item, i) in arrOfSelectedItems">
          <div v-if="item.type === ''"
            class="vue-close"
            @click="deleteItem(item.id)">
              <span></span>
              <span></span> 
          </div>
          <div class="card-items__title">
            {{item.type}}
          </div>
          <div class="card-items__img-wrapper">
            <img :src="item.preview" :alt="item.type">
          </div>
        </div>
    </div>
    <div class="card-result">
      <div class="card-result__fillprice">
        <div>{{global.texts.sum}} {{this.spacify(fullPrice)}} {{global.texts.currency}}</div>
      </div>
      <button 
        @click="openedPopUp = true" 
        type="button" 
        class="vue-btn button button_green">
        {{global.texts.btns}}
        <span class="button__blip button__blip_hover"></span>
        <span class="button__blip button__blip_click"></span>
      </button>
      <PopUpFormSubmit 
        @close="openedPopUp = false" 
        :opened="openedPopUp"
        :global="global"
        :postLink="global.form.postLinks.fullPrice">
        <component 
          :is="'input'" 
          :name="`nameOfForm`"
          type="hidden"
          :value="2">
        </component>
        <component 
          :is="'input'" 
          :name="`fullPrice`"
          type="hidden"
          :value="this.$store.state.total_sum">
        </component>
        <component 
          :is="'input'" 
          :key="i"
          :name="`item${i}`"
          type="hidden"
          v-for="(item, i) in arrOfSelectedItems"
          :value="`${item.type}, ${item.name}`">
        </component>
      </PopUpFormSubmit>
    </div>
  </div>
</template>

<script>
import PopUpFormSubmit from './PopUpFormSubmit.vue';
import {globalMixin} from './globalMixin.js'

export default {
  name: 'TheCard',
  mixins: [globalMixin],
  data() {
    return {
      openedPopUp: false
    } 
  },
  components: {
    PopUpFormSubmit
  },
  props: {
    items: {
      type: Array,
      required: true
    },
    global: {
      type: Object, 
      required: true
    }
  },
  computed: {
    currentItem() {
      return this.$store.state.current_item
    },
    selectedItems() {
      return this.$store.state.selected[this.currentItem];
    },
    arrOfSelectedItems() {
      const selectedItems = this.$store.state.selected;

      let items = [];
      for(const i in selectedItems) {
        items.push(i)
      };

      let arr = [];
      for (let i = 0; i < items.length; i++) {
        arr.push(selectedItems[i]);
      }

      let res = [];

      for (let i = 0; i < items.length; i++) {
        let walls = arr[i][''];
        let flor = arr[i][''];
        let dors = arr[i][''];
        let additionals = arr[i][''];

        for (const key in additionals) {
          let check = (additionals[key] === true) ? this.items[i].filters[3].items[key] : null;
          if(check !== null) res.push(check);
        }

        let florItem = null;
        let dorsItem = null;
        let wallItem = null;

        if(this.items[i] !== undefined && this.items[i].filters[1] !== undefined) {
          florItem = this.items[i].filters[1].items[flor];
        }
        if(this.items[i] !== undefined && this.items[i].filters[2] !== undefined)  {
          dorsItem = this.items[i].filters[2].items[dors];
        }
        if(this.items[i] !== undefined) {
          wallItem = this.items[i].filters[0].items[walls];
        }

        if(florItem !== null) res.push(florItem);
        if(dorsItem !== null) res.push(dorsItem);
        if(wallItem !== null) res.push(wallItem);
      }

      let arrRes = [];
      for(const item of res) {
        arrRes.push([item.type, item.name])
      }

      this.$store.commit('SET_ARR_RESULT', arrRes);

      return res;
    },

    fullPrice() {
      const selectedItems = this.$store.state.selected,
            flatPrice = this.global.flat_price,
            flatSquare = this.global.flat_square,
            squarePrice = this.global.square_price;

      const total = flatPrice + (flatSquare * squarePrice);
      
      let sum = 0,
        items = [],
        arr = [];

      sum += total;

      for(const i in selectedItems) {
        items.push(i)
      };

      for (let i = 0; i < items.length; i++) {
        arr.push(selectedItems[i]);
      }

      
      for (let i = 0; i < items.length; i++) {
        let additionals = arr[i][''];

        for (const key in additionals) {
          sum += (additionals[key] === true) ? parseInt(this.items[i].filters[3].items[key].price) : 0;
        }
      }
      this.$store.commit('SUM', sum)

      return sum;
      
    }
  },
  methods: {
    deleteItem(id) {
      this.$store.commit('DELETE_EQUPMENT', id)
    }
  }
  
}
</script>


