<template>
  <div class="vue-filter">
    <ul class="vue-filter__list">
      <li
        v-if="selectItem"
        :key="i"
        v-for="(item, i) in items[currentItem].filters"
        @click="showItems(i)"
        :class="{'vue-filter-list_active': opened && currentFilter === i}">
        <div class="vue-filter__item"
         @click="openSubMenu(i)"
         :class="{'vue-filter-item_active': opened && currentFilter === i}">
          <span>{{item.name}}</span>
          <span class="vue-filter__pointer"
            :class="{'vue-filter__pointer_active': opened && currentFilter === i}"></span>
        </div>
        <div
          class="vue-filter-row"
          :class="{'vue-filter-row_active': opened && currentFilter === i}">
          <div
            :class="{'vue-filter-row__col_active': $store.state.selected[currentItem][itemFilter.name] === i || $store.state.selected[currentItem][itemFilter.name][i] === true}"
            :key="i"
            v-for="(item, i) in itemFilter.items"
            class="vue-filter-row__col">
            <div
              @click="selectItem(i, itemFilter.name)"
              class="vue-filter-items__preview">
              <img
                class="vue-filter-items__img"
                :src="item.preview">
              <!-- item.preview -->
            </div>
            <div
              class="vue-filter-items__content">
              <div @click="selectItem(i, itemFilter.name)"
                class="vue-filter-items__title">
                {{item.name}}
              </div>
              <div
                class="vue-filter-items__about"
                @click="setCurrentPopUpData(i), popUp = !popUp" ></div>
            </div>
          </div>
      </div>
      </li>
    </ul>
    <!-- :class="{'vue-filter-items_active': opened}" -->
    <div class="vue-filter-items">
      <div class="vue-filter-items__header">
        <div v-if="checker"
          class="vue-filter-items__currentItem">{{itemFilter.name}}</div>
        <div class="vue-close" @click=" popUp = false">
          <span></span>
          <span></span>
        </div>
      </div>
    </div>
    <div v-if="popUp" class="vue-filter-popup">
      <div class="vue-close" @click="popUp = !popUp">
        <span></span>
        <span></span>
      </div>
      <h6>{{items[currentItem].filters[currentFilter].items[currentPopUop].name}}</h6>
      <p>{{items[currentItem].filters[currentFilter].items[currentPopUop].about}}</p>
    </div>
  </div>
</template>

<script>

import { VueMaskDirective } from 'v-mask'
export default {
  name: 'TheFilters',
  directives:{
    mask: VueMaskDirective
  },
  computed: {
    itemFilter(){
      let {items, currentItem, currentFilter} = this;
      if(currentFilter > -1) {
      return items[currentItem].filters[currentFilter];
    }else{
      return {};
    }
    },
    currentItem() {return this.$store.state.current_item},
    checker() {
      if(this.items[this.currentItem].filters[this.currentFilter] === undefined) {
        //this.opened = false;
        return false;
      }
      else {
        return true;
      }
    },
    active() {
      let obj = this.$store.state.selected[this.items[this.currentItem].name];
      return obj;
    },
    item() {
      return this.$store.state.current_item;
    }
  },
  data() {
    return {
      opened: false,
      currentFilter: -1,
      currentPopUop: 0,
      popUp: false,
      selectedItems: [],
      first: true
    }
  },
  props: {
    items: {
      type: Array,
      required: true
    }
  },
  watch: {
    item(value) {
      this.opened = false;
      // this.currentFilter = -1;
      this.currentPopUop = 0;
      this.popUp = false;
      this.first = true;
    }
  },
  methods: {
    selectItem(i, name) {
      const currentItem = this.currentItem;
      if(name === '') {
        this.$store.commit('SET_EQUPMENT', {currentItem, name, i});
        return;
      }
      this.$store.commit('SET_FILTERED_ITEM', {currentItem, name, i});
    },
    showItems(i) {
      if(this.currentFilter === i && !this.opened) {
        this.currentFilter = -1;
      } else{
          this.currentFilter = i;
      }
    },
    setCurrentPopUpData(i){
      this.currentPopUop = i;
    },
    openSubMenu(i) {
      this.opened = true;

      if(this.currentFilter === i) {
        this.opened = false;
      }

       else if(this.first) {
        this.opened = true;
        this.first = false;
      }
    }
  }
}
</script>



