$(document).ready( () => {
    let checkBoxes = $('.filter_commercial .checkbox__checkbox');
    checkBoxes.on('change', function(e) {
        if ( !$(this).parent('label').siblings('label').find('input').is(":checked") ) {
            $( this ).prop( 'checked', 'checked' );
        }
    })
} )



