// BAR Chart
$(".chart-bar").each(function(i, el){
  var $el = $(el);
  var ctx = $el.find("canvas");
  var data;

  try {
    data = JSON.parse($el.find("script").text());
    buildChartBar(data, ctx);
  } catch (e) {
    console.error(e);
  }
})

function buildChartBar(data, ctx) {

  var chart = new Chart(ctx, {
    type: 'bar',
    data: data,
    options: {
        scales: {
            yAxes: [{
              ticks: {
                min: 0,
              }
            }]
        }
    }
  });
}


