$(document).ready(function() {
  $('.new-quality .zoom-img, .apartment-description__img .zoom-img ').click(function() {
    let img = $(this);
    let src = img.attr('src');

    $('body').append('<div class="popup-img">' +
      '<div class="popup-img__bg"></div>' +
      '<img src="' + src + '"class="popup-img__large">' +
      '<div class="popup-img__close"></div>' +
    '</div>');

    $('.popup-img').fadeIn(500);
    $('.popup-img__bg, .popup-img__close').click(function() {
      $('.popup-img').fadeOut(500);
      setTimeout(function() {
        $('.popup-img').remove();
      },500);
    });
  });
});



