<template>
  <transition name="vue-modal">
    <div class="vue-modal vue-modal_mask"
      v-if="opened">
      <div class="vue-modal__form">
        <div
          class="vue-close"
          @click="close">
            <span></span>
            <span></span>
        </div>
        <slot name="title"></slot>
        <form
          @submit=" submit "
          class="vue-form"
          ref="form">
            <input
              name="id_flat"
              class="input form__input"
              type="hidden"
              :value="`${this.global.flat_id}`"/>
            <input
              name="name"
              class="input form__input"
              type="text"
              :value="name_value"
              @input="(event) => name_value = event.target.value"
              :placeholder="this.global.form.fields[0].name"
              required/>

            <input
              name="tel"
              class="input form__input input_phone"
              :value="email_value"
              v-mask="'+7(###)###-##-##'"
              @input="(event) => email_value = event.target.value"
              type="tel"
              required
              :placeholder="this.global.form.fields[1].name"/>


            <slot></slot>

          <button
            class="button form__button"
            type="submit"
            ref="submit">
            {{ state }}
            <span class="button__blip button__blip_hover"></span>
            <span class="button__blip button__blip_click"></span>
          </button>
        </form>
      </div>
    </div>
  </transition>
</template>

<script>
import { VueMaskDirective } from 'v-mask';
import axios from 'axios';

export default {
  name: 'PopUpFormSubmit',
  directives:{
    mask: VueMaskDirective
  },
  data() {
    return {
      state: this.global.form.button,
      name_value: '',
      email_value: ''
    };
  },
  props: {
    opened: {
      type: Boolean
    },
    postLink: {
      type: String,
      required: true
    },
    global: {
      type: Object,
      required: true
    }
  },
  methods: {
    clearForm() {
      let form = this.$el.querySelector('.vue-form'),
        inputs = this.$el.querySelectorAll("input");

      this.name_value = '';
      this.email_value = '';

      for(const item of inputs) {
        item.setAttribute('value', '');
        item.value = '';
      }

      form.reset();
      this.state = this.global.form.button;
      this.close();
    },
    close() {
      this.$emit("close");
    },
    submit(e) {
      e.preventDefault();
      //this.$el.querySelector('.form__button').blur();

      this.state = this.global.form.button_sending;
      console.log(this.state)

      this.postNow();
    },
    postNow() {
      const formData = new FormData(this.$el.querySelector('.vue-form'));

      axios.post(
        this.postLink,
        formData,
        { headers: {'Content-Type': 'multipart/form-data'}})
        .then((r) => {
            console.log(r)
            this.state = this.global.form.sended;
            setTimeout(() => {
              this.clearForm();
            }, 2000)
          })
        .catch(e => {
          console.log('error')
          if (e) console.error(e);
          this.state = this.global.form.error;
          setTimeout(() => {
              this.clearForm();
            }, 2000)
        });
    }
  }
}
</script>

<style>

</style>



