;(function(){
window.scaleCtrl = {
	init: function(){

		this.scaleFilling();

	},
	scaleFilling: function(){

		$('[data-progress]').each(function(){

			var $scale = $(this);
			var value = $scale.data('progress');

			$scale.css("width", ""+ value +"%");
		})
	}
}
})();



