/**
 * Home page scripts.
 *
 * @module ConstructorFlat
 */
import Vue from 'vue';
import TheConstructor from '../componentsVue/TheConstructor.vue';
import Vuex from 'vuex';
import { mapState } from 'vuex';
Vue.use(Vuex);

// import { Resp, throttle } from '../modules/dev/_helpers';

export default class ConstructorFlat {
  /**
   * Cache data, make preparations and initialize page scripts.
   */
  
  constructor() {
    this.body = document.body;
    this.sectionExist = false;
    
    this.init();
  }

  initApp() { 
    const store = new Vuex.Store({
      state: {
        total_sum: 0,
        current_item: 0,
        openedFilterItems: false,
        arrResult: [],
        selected: {
          0: {
            "": 0,
            "": 0,
            "": 0,
            "": {
              '0': false, 
              '1': false,
              '2': false,
              '3': false, 
              '4': false,
              '5': false,
              '6': false, 
              '7': false,
              '8': false,
              '9': false
            }
          },
          1: {
            "": 0,
          }
        }
      },
      mutations: {
        SUM(state, sum) {
          state.total_sum = sum;
        },
        SET_ARR_RESULT(state, item) {
          state.arrResult = item;
        },
        SET_CURRENT_ITEM (state, item) {
          state.current_item = item;
        },
        DELETE_EQUPMENT(state, id) {
          state.selected[0][''][id] = false;
        },
        SET_EQUPMENT (state, item) {
          state.selected[item.currentItem][item.name][item.i] = !state.selected[item.currentItem][item.name][item.i];
        },
        SET_FILTERED_ITEM (state, item) {
          state.selected[item.currentItem][item.name] = item.i;
        }
      }
    })
    const app = new Vue({
      el: '#vueApp',
      store,
      components: {
        TheConstructor
      },
      computed: mapState([
        'current_item'
      ])
    });
  }

  checkExistingOfConstructorBlock() {
    this.body.querySelector('[data-section="vue-constructor"]') !== null ? this.sectionExist = true : this.sectionExist = false;
    if(this.sectionExist === true) {
      this.initApp();
    }
  }
  init() {
    this.checkExistingOfConstructorBlock();
  }
}

const ConstructorScript = new ConstructorFlat;



