<template>
  <div class="vue-app-flat-tabs">
    <ul class="vue-app-flat-tabs__list">
      <li class="vue-app-flat-tabs__items"
        :key="i"
        v-for="(item, i) in items"
        @click="setCurrentItem(i)"
        :class="{'vue-app-flat-tabs__items_active' : $store.state.current_item === i}">
        {{item.name}}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'TheTabs',
  props: {
    items: {
      type: Array,
      required: true
    }
  },
  methods: {
    setCurrentItem(item) {
      this.$store.commit('SET_CURRENT_ITEM', item);
    }
  }
}
</script>


