;(function(){
  
window.circleProgress = {
	init: function() {

		$('[data-circle-progress]').each(function() {
			var $block = $(this);

			var val = +$block.data('circle-progress');

			if (val <= 14) {
				val = '10';
				$block.find(".circle-progress__progress_" + val).addClass('circle-progress__progress_active');
				return;
			}

			if (val <= 24) {
				val = '20';
				$block.find(".circle-progress__progress_" + val).addClass('circle-progress__progress_active');
				return;
			}

			if (val <= 34) {
				val = '30';
				$block.find(".circle-progress__progress_" + val).addClass('circle-progress__progress_active');
				return;
			}

			if (val <= 44) {
				val = '40';
				$block.find(".circle-progress__progress_" + val).addClass('circle-progress__progress_active');
				return;
			}

			if (val <= 54) {
				val = '50';
				$block.find(".circle-progress__progress_" + val).addClass('circle-progress__progress_active');
				return;
			}

			if (val <= 64) {
				val = '60';
				$block.find(".circle-progress__progress_" + val).addClass('circle-progress__progress_active');
				return;
			}

			if (val <= 74) {
				val = '70';
				$block.find(".circle-progress__progress_" + val).addClass('circle-progress__progress_active');
				return;
			}

			if (val <= 84) {
				val = '80';
				$block.find(".circle-progress__progress_" + val).addClass('circle-progress__progress_active');
				return;
			}

			if (val <= 99) {
				val = '90';
				$block.find(".circle-progress__progress_" + val).addClass('circle-progress__progress_active');
				return;
			}

			if (val === 100) {
				val = '100';
				$block.find(".circle-progress__progress_" + val).addClass('circle-progress__progress_active');
				return;
			}

		});

	}
};

})();



