export default class GetInTouchTab {
  constructor (btnsClass){
    this.btns = [...document.querySelectorAll(`.${btnsClass}`)];
    this.tabs = [...document.querySelectorAll('.get_in_touch__tabs-tab')];
    this.holder = this.tabs[0].parentElement;
    this.btnActive = 'get_in_touch__tabs-btn-active';
    this.tabActive = 'get_in_touch__tabs-tab-active';
    this.tabVisible = 'get_in_touch__tabs-tab-visible';
    this.init();
  }
  init() {
    for (let i = 0; i < this.btns.length; i++){
      this.btns[i].addEventListener('click', ()=> {
        for(let j = 0; j < this.btns.length; j++){
          this.btns[j].classList.remove(this.btnActive);
          this.tabs[j].classList.remove(this.tabActive, this.tabVisible);
        }
        this.btns[i].classList.add(this.btnActive);
        this.tabs[i].classList.add(this.tabActive);
        this.setMinHeight(i);
        setTimeout(()=> {
          this.tabs[i].classList.add(this.tabVisible);
        }, 300)
      })
    }
    this.setMinHeight(0)
  }

  setMinHeight( index ) {
    let height = 0;
    for ( let child of this.tabs[index].children ) {
      height += child.clientHeight;
    }
    this.holder.style.minHeight = height + 'px';
  }
}


