<template>
  <div class="vue-additionalForms">
    <h6>{{global.form.afterFilteForm.title}}</h6>
    <div class="vue-additionalForms__row">
      <div 
        :key="i"
        v-for="(item, i) in global.form.afterFilteForm.forms"
        class="vue-additionalForms__link">
        <button class="vue-additionalForms__btn"
          @click="openedPopUp[i] = true"
          type="button" >
          {{item.name}} >
        </button>
        <PopUpFormSubmit 
          @close="openedPopUp[i] = false" 
          :opened="openedPopUp[i]"
          :global="global"
          :postLink="item.postLinks">
          <div slot="title" class="vue-modal__title">
              {{spacify(fullPriceForAdditionalWorks)}} 
          </div>
          <component 
            :is="'input'" 
            type="hidden"
            :name="'nameOfForm'"
            :value="item.id">
          </component>
          <component 
            :is="'input'" 
            :name="`fullPrice`"
            type="hidden"
            :value="$store.state.total_sum">
          </component>
          <component 
            :is="'input'" 
            :key="i"
            :name="`item${i}`"
            type="hidden"
            v-for="(item, i) in selectedItems"
            :value="`${item[0]}, ${item[1]}`">
          </component>
          <component 
            v-if="item.name === '  '"
            :is="'input'" 
            type="hidden"
            :name="'forManager'"
            :value="'  '">
          </component>
        </PopUpFormSubmit>
      </div>
    </div>
  </div>
</template>

<script>
import PopUpFormSubmit from './PopUpFormSubmit.vue';
import {globalMixin} from './globalMixin.js';

export default {
  name: 'TheFiltersForms',
  mixins: [globalMixin],
  data() {
    return {
      openedPopUp: {
        "0" : false,
        "1" : false
      } 
    }
  },
  components: {
    PopUpFormSubmit
  },
  props: {
    items: {
      type: Array,
      required: true
    },
    global: {
      type: Object, 
      required: true
    }
  },
  computed: {
    fullPriceForAdditionalWorks() {
      return parseInt(this.$store.state.total_sum);
    }, 
    selectedItems() {
      let items = this.$store.state.arrResult;
      return items;
    }
  }
  
}
</script>


