<template>
  <div class="vue-app-flat" v-if="response !== null">
    <div v-if="preloader" class="vue-app-flat-preloader">
      <img class="vue-app-flat-preloader__img" :src=" response.global.loadingSrc " alt="loading">
    </div>
    <div
      class="vue-app-flat__content">
      <!-- tabs -->
      <div class="wrap wrap_mobile_full">
        <TheTabs :items=" response.items " />
      </div>
      <!-- ./tabs -->
      <!-- body -->
      <div class="wrap wrap_mobile_full">
        <div class="vue-app-body">
          <div class="vue-app-body__imgBuilder">
            <TheImageBuilder
              :items=" response.items "/>
          </div>
          <aside class="vue-app-body__aside">
            <TheFilters class="vue-app-body__filters"
              :items=" response.items "/>
            <TheFiltersForms class="vue-app-body__forms"
              :global=" response.global"
              :items=" response.items "/>
          </aside>
        </div>
      </div>
      <!-- footer -->
      <div class="vue-app-footer">
        <TheCard
          :global=" response.global "
          :items=" response.items "/>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';
  import TheTabs from './TheTabs.vue';
  import TheImageBuilder from './TheImageBuilder.vue';
  import TheFilters from './TheFilters.vue';
  import TheFiltersForms from './TheFiltersForms.vue';
  import TheCard from './TheCard.vue';
  import PopUpFormSubmit from './PopUpFormSubmit.vue';

  export default {
    name: 'TheConstructor',
    components: {
      TheTabs,
      TheImageBuilder,
      TheFilters,
      TheFiltersForms,
      TheCard,
      PopUpFormSubmit
    },
    data() {
      return {
        title: 'The constructor',
        response: null,
        preloader: true
      }
    },
    created() {
      const elId = document.body.querySelector('#apartment_id') === null ? null : document.body.querySelector('#apartment_id').value
      axios.get('/apartment/constructor/index.json', { 
        params: {
          id: elId
        }
      })
        .then((r) => {
          this.response = r.data;
          setTimeout(() => {
            this.preloader = false;
          }, 1500)
        })
        .catch((e) => {
          this.preloader = true;
          console.error(e);
        })
    }
  }
</script>



