function expandableList() {

  $(document).on('click', '[data-list-show]', function(e){

    e.preventDefault();

    var $btn = $(this);
    var id = $btn.data('list-show');
    if ( ! id ) { console.error('No list id in ', e.target ); return; }

    var $list = $('[data-list-items="' + id + '"]');
    if ( ! $list.length ) { console.error('No list for button ', e.target ); return; }

    var showState = $btn.hasClass('js-state-show');

    if ( showState ) {
      $btn.removeClass('js-state-show');
      $list.slideUp();
    } else {
      $btn.addClass('js-state-show');
      $list.slideDown();
    }
  })
}

export { expandableList };



