;(function(){

  window.customScroll = {
	init: function() {

		var th = customScroll;

		th.data = {
			isMove: 1,
			offset: $('.poster').height() + 5,
			lastScrollTop: 0
		};

		if (!client.isMobile && $(window).scrollTop() < th.data.offset) {

		}

		th.events();
	},
	bring: function(dir) {

		var th = customScroll;

		var offset = 0;
		if (dir) {
			offset = th.data.offset;
		}

		$('html, body').animate({
			scrollTop: offset
		}, 500, function() {
            th.data.isMove = 1;
			if (dir) {
				setTimeout(function() {
					th.data.isMove = 1;
				}, 800);
			} else {
				th.data.isMove = 1;
			}
		});

	},

	events: function() {

		var th = customScroll;

		var func = function(e) {
			var s = $(this).scrollTop();

			if (client.isMobile) return

			if ($(window).scrollTop() < th.data.offset) {
				e.preventDefault();
			}

			if (!th.data.isMove) {
				e.preventDefault();
				return;
			}

			var delta = e.deltaY || e.detail || e.wheelDelta || e.originalEvent.deltaY || e.originalEvent.wheelDelta*-1;

			if (s < th.data.offset && th.data.isMove && delta > 0) {
				e.preventDefault();
				th.data.isMove = 0;
				th.bring(1);
			}

			//console.log(s, th.data.offset, delta);
			if (s < th.data.offset && th.data.isMove && delta < 0) {
				e.preventDefault();
				th.data.isMove = 0;
				th.bring(0);
			}
		}

		if ('onwheel' in document) {
			$(window).on("wheel", func);
		} else if ('onmousewheel' in document) {
			$(window).on("mousewheel", func);
		} else {
			$(window).on("MozMousePixelScroll", func);
		}

		/*$(window).on('scroll', function(e) {
			var s = $(this).scrollTop();
			if (s < th.data.offset) {
				e.preventDefault();
			}
		});*/


		$(window).on('resize', function() {
			th.data.offset = $('.poster').height();
		});




		/*$(window).on('mousemove', function(e) {
			if (th.data.isMove && !th.data.isBusy) {
				e.preventDefault();
				th.data.isBusy = 1;
				th.bring();
			}
		});*/

	}
};

})()



