(function(){
  var $scrlBtn = $('[data-scroll-subscribe]');
  if ( ! $scrlBtn.length ) return;

  var $el = $( $scrlBtn.attr('href') );
  if ( ! $el.length ) return;

  var offset = $('#header-black').height() || 0;

  $scrlBtn.click(function(e){
    e.preventDefault();
    $('html, body').animate({
      scrollTop : $el.offset().top - offset
    }, 700);
  })
})()



