var talanApp = {

	init: function() {
		tools.init(['backgrounds', 'preloader', 'modals', 'tabs']);
		this.initPlugins();
	},
	initPlugins: function() {
		if($('.get_in_touch').length){
      let mobDropDown = new Dropdown();
      let tabs = new GetInTouchTab('get_in_touch__tabs-btn');
      let tabsMobile = new GetInTouchTab('dropdown_item__link');
		}
		// slick
		var $slickSliders = $("[data-slick]");
		if ($slickSliders.length) {
			$slickSliders.slick();
		}

		$(window).on('resize', function() {
			if ($slickSliders.length) {
				$slickSliders.slick('setPosition');
			}
		});

		if ($("[data-testimonials-slider]").length) {
			testimonialsCtrl.init();
		}

		if ($("[data-readiness-slider]").length) {
			readinessCtrl.init();
		}

		if ($("[data-stavka-slider]").length) {
			stavkaCtrl.init();
		}

		if ($("[data-gallery-slider]").length) {
			careerCtrl.init();
		}


		if ($("[data-shares-slider]").length) {
			sharesSliderInit.init();
		}

    if ($("[data-partners-slider]").length) {
      partnersSlider.init();
    }

		if ($("[data-youtube]").length) {
			videoCtrl.init();
		}

		if ($("#preloader").length) {
			talanPreloader.init();
		}

		if ($("header").length) {
			headerCtrl.init();
		}

		if ($("[data-scroll-down]").length) {
			scrollDownCtrl.init();
		}

		if ($('[data-progress]').length) {
			scaleCtrl.init();
		}

		if ($(".panel-menu").length) {
			menuCtrl.init();
		}

		if ($('.poster').length) {
			posterVideoCtrl.init();
		}

		if ($('.panel-menu, header, .complexes').length) {
			menuDropdownCity.init();
		}

		if ($('.modals').length) {
			popupCtrl.init();
		}

		if ($('[data-popup-city]').length) {
			popupCityCtrl.init();
		}

		// wrap article $tables
		var $tables = $('.article table');
		if ( $tables.length ) wrapTables( $tables );

		// init expandable lists
		if ( $('[data-list-show]').length ) expandableList();


		if ($('[data-btn-advantage]').length) {
			advantageMoreCtrl.init();
		}

		if ($('[data-btn-architect]').length) {
			architectMoreCtrl.init();
		}

		if ($('[data-circle-progress]').length) {
			circleProgress.init();
		}


		forms.init();

		if ($("[data-form]").length) {
			formHandler.init();
		}

		if ($(".filters").length) {
			searchFilters.init();
		}

		if ($("[data-custom-select]").length) {
			customSelect.init();
		}

		if ($('.poster').length && !client.isMobile) {
			customScroll.init();
		}

		// construction course
		var $ccContainer = $('.course_of_construction');
		if ( $ccContainer.length ) {
			new ConstructionCourse($ccContainer);
		}

		// shares matchHeight
		var $shareCards = $('.shares__card');
		if ( $shareCards.length ) {
			$shareCards.matchHeight();
		}

		// payment calc init
		if ( $(".calc_form").length ) {
			paymentCalc.init();
		}

		if ($('.commercial__gallery').length){
      initCommercialProperty();
		}

		$(document).on('preloaderDone', function() {
			new WOW({
				offset: 50
			}).init();
		});
	}
};

$(document).ready(function() {

	talanApp.init();

	if (client.isMobile) {
		var $poster = $('.poster');
		var posterHeight = $poster.height();
		$poster.css('height', posterHeight);
	} else {
		var $poster = $('.poster');
		var posterHeight = $poster.height();
		$poster.css('height', '100vh');
	};


});

$(window).on('resize', function() {
	if (client.isMobile) {
		var $poster = $('.poster');
		$poster.css('height', 'auto');
		var posterHeight = $poster.height();
		if (posterHeight > client.windowW) {
			$poster.css('height', posterHeight);
		} else {
			$poster.css('height', '100vh');
		};
	} else {
		var $poster = $('.poster');
		var posterHeight = $poster.height();
		$poster.css('height', '100vh');
	};
});

$(window).on('load', function(){
  setTimeout(function() {
    $('.preloader').addClass('preloader_done');
  }, 1500)
})

$(document).ready(function() {
  $('.questions__form input').on('focus', function(event) {
		let hidePopup = $('.popup_presentation');
		console.log(hidePopup);
		hidePopup.addClass('hide_popup');
	})
});

var is_safari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);



